<template>
  <div class="db-main">
    <div class="db-inner">
      <div class="db-inner__template">
        <h1 class="db-inner__template-title">
          Ваш Бiзнес
        </h1>
        <p class="db-inner__template-subtitle">
          Адмiнiстрування
        </p>
        <component class="py-5" :data="data" :is="curProjTab"></component>
      </div>
    </div>
  </div>
</template>

<script>

import { mapState, mapActions } from 'vuex';

export default {
  name: 'business',
  data() {
    return {
      data: '',
    }
  },
  computed: {
    ...mapState([
      'curProjTab'
    ]),
  },
  components: {
    DashInfo: () => import('@/components/dashboard/productTabs/InfoTab'),
    DashProduct: () => import('@/components/dashboard/productTabs/ProductsTab'),
    DashOrders: () => import('@/components/dashboard/productTabs/OrdersTab'),
    DashNews: () => import('@/components/dashboard/productTabs/NewsTab'),
    DashSettings: () => import('@/components/dashboard/productTabs/SettingsTab'),
  },
  methods: {
    ...mapActions([
      'GET_INFO_PROJ'
    ])
  },
  async created() {
    const data = await this.GET_INFO_PROJ()
    this.data = data.data
    // console.log(data)
  },
}
</script>

<style lang="scss" scoped>

.db-inner {
  
  &__template {
    padding: 30px;
    
      &-title {
        color: #343a40;
        margin-bottom: 5px;
        font-size: 1.5rem;
        font-weight: 500;
      }
  }
}
</style>